@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,700;1,200&display=swap);
body {
  background: rgb(18,18,18) !important;
  background: linear-gradient(0deg, rgba(18,18,18,1) 10%, rgba(76,221,195,1) 110%) !important;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.main {
  font-family: "Poppins", sans-serif;
  color: #efefef;
}
.nav {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
}

.logout-button {
  font-size: 1.1rem !important;
  color: #e0e0e0 !important;
  font-family: "Poppins", sans-serif !important;
  font-weight: 400 !important;
  text-transform: none !important;
}
.heading-text {
  font-family: "Poppins", sans-serif !important;
  font-weight: 900 !important;
  letter-spacing: -0.1rem !important;
  color: #e0e0e0;
}

.small-heading-text {
  font-family: "Poppins", sans-serif !important;
  font-weight: 900 !important;
  color: #e0e0e0;
  display: -webkit-box !important;
  -webkit-line-clamp: 2 !important;
  -webkit-box-orient: vertical !important;
  overflow: hidden !important;
}

.heading-text-margin {
  margin: 1rem !important;
  font-family: "Poppins", sans-serif !important;
  font-weight: 900 !important;
  letter-spacing: -0.1rem !important;
  color: #e0e0e0;
}

.body-text {
  margin-top: 1rem !important;
  font-family: "Poppins", sans-serif !important;
  font-weight: 400 !important;
  color: #e0e0e0;
}

.App {
  text-align: center;
}

.container {
  padding: 5px !important;
}

.menu {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}

.App-header {
  margin-top: 2rem;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo-div {
  margin: 0.7rem;
}

.logo {
  height: auto;
  max-width: 175px;
}

.cards {
  margin-top: 3rem;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  width: 100%;
}

.card-div1 {
  -webkit-animation: vibrate-1 10s ease-in-out infinite backwards;
          animation: vibrate-1 10s ease-in-out infinite backwards;
}

.card-div2 {
  -webkit-animation: vibrate-1 7s ease-in-out infinite backwards;
          animation: vibrate-1 7s ease-in-out infinite backwards;
  margin-right: -20px;
  z-index: -2;
}

.card-div3 {
  -webkit-animation: vibrate-1 8s ease-in-out infinite backwards;
          animation: vibrate-1 8s ease-in-out infinite backwards;
  margin-left: -20px;
  z-index: -2;
}

.card {
  border-radius: 1.2rem;
  box-shadow: -5px 9px 16px -10px rgba(0, 0, 0, 0.75);
  align-self: center;
  height: auto;
  max-width: 100%;
}

@-webkit-keyframes vibrate-1 {
  0% {
    transform: translate(0) scale(1);
  }
  20% {
    transform: translate(-1px, 1px) scale(1.01);
  }
  40% {
    transform: translate(-2px, -2px) scale(1);
  }
  60% {
    transform: translate(1px, 1px) scale(1.01);
  }
  80% {
    transform: translate(1px, -1px) scale(1);
  }
  100% {
    transform: translate(0) scale(1);
  }
}

@keyframes vibrate-1 {
  0% {
    transform: translate(0) scale(1);
  }
  20% {
    transform: translate(-1px, 1px) scale(1.01);
  }
  40% {
    transform: translate(-2px, -2px) scale(1);
  }
  60% {
    transform: translate(1px, 1px) scale(1.01);
  }
  80% {
    transform: translate(1px, -1px) scale(1);
  }
  100% {
    transform: translate(0) scale(1);
  }
}

.login-button {
  text-align: left;
}

.small-button {
  margin: 10px !important;
  background-color: #121212 !important;
  color: #e0e0e0 !important;
  font-family: "Poppins", sans-serif !important;
  font-weight: 900 !important;
  text-transform: none !important;
  margin-top: 1.5rem !important;
  width: 135px !important;
  height: 40px !important;
  border-radius: 40px !important;
}

.large-button {
  background-color: #121212 !important;
  color: #e0e0e0 !important;
  font-family: "Poppins", sans-serif !important;
  font-weight: 900 !important;
  text-transform: none !important;
  margin-top: 1.5rem !important;
  width: 175px !important;
  height: 40px !important;
  border-radius: 40px !important;
}

.reg-button {
  margin: 10px !important;
  border-radius: 40px !important;
}

.App-link {
  color: #0f0f0f;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.artistDiv {
  max-width: 65vw;
  margin-top: 5%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 1px solid white;
}

.content {
  margin-top: 1rem !important;
  display: flex !important;
  justify-content: center;
  padding-left: 5px !important;
  padding-right: 5px !important;
}

.heading-margin {
  margin: 30px 0px 30px 0px !important;
}

.card-link {
  text-decoration: none !important;
}

.artist-info {
  text-align: left;
}

.artist-name {
  font-weight: 700;
}

.text {
  text-align: left;
  font-family: "Poppins", sans-serif;
  font-size: 1.8rem;
}

.section {
  margin: 75px;
}
.count {
  color: grey;
  font-style: italic;
  font-weight: 200;
}

.image {
  align-self: center;
  height: auto;
  width: 100%;
}

@media only screen and (min-width: 600px) {
  .image {
    width: 50%;
  }
  .artistName {
    font-size: 2rem !important;
    white-space: nowrap;
  }
}

